import React, { Component, useState, useEffect } from "react";
import { Card, Container, Row, Col, Button } from "react-bootstrap";
import { readRemoteFile } from "react-papaparse";
import { WebNav } from "../../components/navbar";
import { CoverHalf } from "../../components/coverHalf";
import { Footer } from '../../components/footer';

// import peopleData from "./peopleData";

function People(props) {
  const [peopleData, setPeopleData] = useState([]);
  const [getData, setGetData] = useState(false);

  useEffect(() => {
    if (!getData) {
      const url =
        "https://docs.google.com/spreadsheets/d/e/2PACX-1vQJDBlUamD2ry930iKqtY0zr9OgOADHLSnOK6PB371S0nvyS6HhBXK82L7jkWePoDz2f9noy9QUMoNz/pub?gid=0&single=true&output=csv";
      readRemoteFile(url, {
        complete: (results) => {
          // console.log("Results:", results);
          setPeopleData(results.data);
          setGetData(true);
        },
      });
    }
  }, [getData]);

  if (getData) {
    return (
      <React.StrictMode>
        <WebNav />
        <CoverHalf />

        <PeopleSection data={peopleData} />
        <Footer />
      </React.StrictMode>
    );
  } else {
    return (
      <React.StrictMode>
        <WebNav />
        <CoverHalf />

        {/* <CurrentMember /> */}
        <Footer />
      </React.StrictMode>
    )
  }

  // return (
  //   <React.StrictMode>
  //     <WebNav />
  //     <CoverHalf />

  //     <PeopleSection data={peopleData} />
  //     <Footer />
  //   </React.StrictMode>
  // )
}

export { People };





function Section(props) {
  let people = props.data

  return (
    people.map(member => (
      <Col xs={12} md={3}>
        <IntroductionCard img={member.image} name={member.name} text={member.description} />
      </Col>
    )))
}

function IntroductionCard(props) {


  return (
    <Card className='my-3 mx-1'>
      <Card.Img variant="top" src={props.img} />
      <Card.Body>
        <Card.Title>{props.name}</Card.Title>
        <Card.Text style={{ minHeight: '1.2rem' }}>{props.text}</Card.Text>
      </Card.Body>
    </Card>
  );
}

