function SelectBtn(props) {
  return (
    <Container fluid>
      <Row>
        <Col xs={3}>
          <Button
            variant="outline-dark"
            className="w-100"
            onClick={() => {
              props.selectChange("principalInvestiagtor");
            }}
          >
            <p>Principal Investiagtor</p>
          </Button>
        </Col>
        <Col xs={3}>
          <Button
            variant="outline-dark"
            className="w-100"
            onClick={() => {
              props.selectChange("currentMember");
            }}
          >
            <p>Current Member</p>
          </Button>
        </Col>
        <Col xs={3}>
          <Button
            variant="outline-dark"
            className="w-100"
            onClick={() => {
              props.selectChange("formerMember");
            }}
          >
            <p>Former Member</p>
          </Button>
        </Col>
        <Col xs={3}>
          <Button
            variant="outline-dark"
            className="w-100"
            onClick={() => {
              props.selectChange("professionalCollaborators");
            }}
          >
            <p>Professional Collaborators</p>
          </Button>
        </Col>
      </Row>
    </Container>
  );
}

function PeopleList(props) {
  return (
    <React.StrictMode>
      {props.list.map((p) => (
        <Container>
          <Row>
            <div className='people-section text-center w-100'>
              <div className='title' style={{ fontSize: '1.5rem', letterSpacing: '0.3rem', fontWeight: '500' }}>
                <p>{p.title}</p>
                <hr />
              </div>
            </div>
          </Row>
          <Row>
            <Section data={p.data} />
          </Row>
        </Container>
      ))}
    </React.StrictMode>
  );
}

function PeopleSection(props) {
  const [peopleType, setPeopleType] = useState("currentMember");
  const [peopleList, setPeopleList] = useState([]);

  useEffect(() => {

    // get 
    var newPeopleList = [];
    for (let i = 1; i < props.data.length; i++) {
      if (peopleType === props.data[i][0]) {


        // search exist
        let keyExist = false
        newPeopleList.forEach(element => {

          if (element.title === props.data[i][1]) {
            keyExist = true;
          }
        });
        if (!keyExist) {
          newPeopleList.push({
            title: props.data[i][1],
            data: [],
          })
        }

        // add
        for (let j = 0; j < newPeopleList.length; j++) {
          if (newPeopleList[j].title === props.data[i][1]) {
            newPeopleList[j].data.push({
              name: props.data[i][2],
              image: props.data[i][3],
              description: props.data[i][4],
              time: props.data[i][5],
              project: props.data[i][6],
            })
          }
        }

      }

    }
    console.log(newPeopleList)
    setPeopleList(newPeopleList);
  }, [peopleType]);

  return (
    <React.StrictMode>
      <SelectBtn
        selectChange={(type) => {
          setPeopleType(type);
        }}
      />
      <PeopleList list={peopleList} />
    </React.StrictMode>
  );
}

